import React from 'react';
import s from './Mainapp.module.css';
import { NavLink} from 'react-router-dom';

const Mainapp = () => {
    return (
      <section>
          <div className={s.frame_wrapper}>

          </div>
          <h3>Choose an experience</h3>
          <nav className={s.mainNav}>
            <NavLink to="/photophilter">
                Photo
            </NavLink>
            <NavLink to="/boomerangphilter">
                Boomerang
            </NavLink>
          </nav>
      </section>
    );
  }

export default Mainapp;