import React, { Component } from 'react';
import Slider from "react-slick";
import s from './Overlayslider.module.css';
import overlay1 from './../../images/overlay1.png';
import overlay2 from './../../images/overlay2.png';
import overlay3 from './../../images/overlay3.png';
import overlay4 from './../../images/overlay4.png';

const philterimage = (e) => {
    document.getElementById('frame_wrapper_overlay').setAttribute('style','background: url("' + e.target.getAttribute('src') + '");    background-repeat: no-repeat; background-size: cover; background-position: center center;');
}


export default class Overlayslider extends Component {
    render() {
      const settings = {
        dots: false,
        infinite: false,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1
      };
      return (
        <div className={s.slider_wrapper}>
          <Slider {...settings}>
            <div className={s.slide_item}>
                <img onClick={philterimage} src={overlay1} alt="overlay1"/>
            </div>
            <div className={s.slide_item}>
                <img onClick={philterimage} src={overlay2} alt="overlay2"/>
            </div>
            <div className={s.slide_item}>
                <img onClick={philterimage} src={overlay3} alt="overlay3"/>
            </div>
            <div className={s.slide_item}>
                <img onClick={philterimage} src={overlay4} alt="overlay4"/>
            </div>
          </Slider>
        </div>
      );
    }
  }