import React from 'react';
import s from './Boomerang.module.css';
import { NavLink} from 'react-router-dom';

const Boomerang = () => {
    return (
      <section>
          <div className={s.frame_wrapper}>

          </div>
          <h3>Choose a frame</h3>
          <nav className={s.mainNav}>
            <NavLink to="/appmenu">
                Return to menu
            </NavLink>
          </nav>
      </section>
    );
  }

export default Boomerang;