import React from 'react';
import s from './Hellomessage.module.css';
import { NavLink} from 'react-router-dom';

const Hellomessage = () => {
    return (
      <section className={s.hello_wrapper}>
          <h1>Your Event Name</h1>
          <p>Your slogan here</p>
          <NavLink to="/appmenu"className="main_btn">Lets start</NavLink>
      </section>
    );
  }
  
  export default Hellomessage;