import React from 'react';
import Header from './header/Header.js';
import Hellomessage from './hellomessage/Hellomessage.js';
import Mainapp from './mainapp/Mainapp.js'
import Photophilter from './photophilter/Photophilter.js'
import Boomerang from './boomerang/Boomerang.js'
import {Route} from 'react-router-dom';
import Form from './form.js';

const App = (props) => {
  return (
      <div className="app_wrapper">
        <Header state={props.appState.Navigation}/>
        <main>
          <Route exact path="/" component={Hellomessage}/>
          <Route path="/appmenu" component={Mainapp}/>
          <Route path="/photophilter" component={Photophilter}/>
          <Route path="/boomerangphilter" component={Boomerang}/>
        </main>
        <Form/>
      </div>
  );
}

export default App;
