import React, { useState } from 'react';
import s from './Photophilter.module.css';
import { NavLink} from 'react-router-dom';
import Overlayslider from './overlayslider/Overlayslider.js';
import Webcam from "react-webcam";


const Photophilter = () => {
  const [image, setImage] = useState({ preview: "", raw: "" });

  const handleChange = e => {
    if (e.target.files.length) {
      setImage({
        preview: URL.createObjectURL(e.target.files[0]),
        raw: e.target.files[0]
      });
      
    }
    document.querySelector('video').classList.remove(s.opacity);
    if (document.querySelector('#firstprev')) {
      document.querySelector('#firstprev').classList.add(s.overall);
    }

    document.querySelector('#cap-p').classList.remove(s.usable);
    document.querySelector('#cap-r').classList.remove(s.usable);
    document.querySelector('#start').classList.remove(s.removed);
    document.querySelector('#savephoto').classList.add('active');
  };
  
  const handleUpload = async e => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("image", image.raw);
  
    await fetch("/photophilter", {
      method: "POST",
      headers: {
        "Content-Type": "multipart/form-data"
      },
      body: formData
    });
  };

  const webcamRef = React.useRef(null);
  const [imgSrc, setImgSrc] = React.useState(null);
  const videoConstraints = {
    width: 375,
    height: 375,
    facingMode: "user"
  };
  const start = () => {
    document.querySelector('video').classList.add(s.opacity);
    if (document.querySelector('#firstprev')) {
      document.querySelector('#firstprev').classList.remove(s.overall);
    }
    
    document.querySelector('#cap-p').classList.add(s.usable);
    document.querySelector('#cap-r').classList.add(s.usable);
    document.querySelector('#start').classList.add(s.removed);
    
  }
  const retake = () => {
    document.querySelector('video').classList.remove(s.active);
    document.querySelector('#cap-r').classList.add('active');
    document.querySelector('#cap-p').classList.remove('active');
    document.querySelector('#savephoto').classList.remove('active');
    
  }
  const capture = React.useCallback(() => {
    const imageSrc = webcamRef.current.getScreenshot({width: 275, height: 275});
    setImgSrc(imageSrc);
    
    
    document.querySelector('video').classList.add(s.active);
    document.querySelector('#cap-r').classList.remove('active');
    document.querySelector('#cap-p').classList.add('active');
    document.querySelector('#savephoto').classList.add('active');
  }, [webcamRef, setImgSrc]);


  const save = async e => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("image", image.raw);
  
    await fetch("/photophilter", {
      method: "POST",
      headers: {
        "Content-Type": "multipart/form-data"
      },
      body: formData
    });
  }

    return (
      <section>
          <div className={s.frame_wrapper}>
          
            {image.preview ? (
              <img src={image.preview} id="firstprev" className={s.overall} alt="dummy" width="100%"/>
            ) : (
              ''
            )}
            <div className={s.frame_wrapper_overlay} id="frame_wrapper_overlay">
            </div>
            <Webcam
              audio={false}
              ref={webcamRef}
              videoConstraints={videoConstraints}
              screenshotFormat="image/jpeg"
            />
            {imgSrc && (
              <img
                src={imgSrc}
              />
            )}
          </div>
          
          <h3 className={s.heading}>Choose a frame</h3>
          <NavLink to="/appmenu" className={s.backlink}>
              Back
          </NavLink>
          <Overlayslider />
          <input
            type="file"
            id="upload-button"
            style={{ display: "none" }}
            onChange={handleChange}
          />
          <button onClick={start} className={s.start} id="start">
            Start
          </button>
          <div className={s.photocontrolls}>
            <button onClick={capture} id="cap-p">Capture photo</button>
            <button onClick={retake} className="active" id="cap-r">Retake photo</button>
            <label htmlFor="upload-button">
              <h5 className={s.uploadlink}>Upload your photo</h5>
            </label>
          </div>
          <button id="savephoto" onClick={save}>Save</button>
          

          {/* <button onClick={handleUpload}>Upload</button> */}
      </section>
    );
  }

export default Photophilter;